import React, { useState, useRef, useContext, useEffect } from 'react';
import { makeStyles } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import { dataFetcher, endpoints } from 'Api';
import { AppContext } from 'Context';
import useSWR from 'swr';
import ReactPlayer from 'react-player';
import screenfull from 'screenfull';
import PlayerControls from './PLayerControls';

const useStyles = makeStyles({
  playerWrapper: {
    width: '100%',
    position: 'relative',
    height: '100vh',
    background: 'black',
    overflow: 'hidden',
  },
});

const format = (seconds) => {
  if (Number.isNaN(seconds)) {
    return '00:00';
  }
  const date = new Date(seconds * 1000);
  const hh = date.getUTCHours();
  const mm = date.getUTCMinutes();
  const ss = date.getUTCSeconds().toString().padStart(2, '0');
  if (hh) {
    return `${hh}:${mm.toString().padStart(2, '0')}:${ss}`;
  }
  return `${mm}:${ss}`;
};

export function VideoPlayer() {
  const { setAppSnackbar } = useContext(AppContext);
  const { videoID } = useParams();
  const [loading, setLoading] = useState(true);
  const [videoDetails, setVideoDetails] = useState({});

  const { mutate: getVideoDetails } = useSWR(
    [endpoints.getVideosDetail, videoID],
    {
      fetcher: (url, id) => dataFetcher(url, { id }),
      onSuccess: ({ success, data }) => {
        if (success) {
          setVideoDetails(data);
        } else {
          setAppSnackbar({
            isVisible: true,
            type: 'error',
            message: 'Oops! Something went wrong',
          });
        }

        setLoading(false);
      },
      onError: () => {
        setLoading(false);
        setAppSnackbar({
          isVisible: true,
          type: 'error',
          message: 'Oops! Something went wrong',
        });
      },
    }
  );
  // console.log('--------------------------------');
  // console.log(videoDetails);
  // console.log('--------------------------------');
  const [state, setState] = useState({
    playing: true,
    muted: false,
    volume: 1,
    playbackRate: 1.0,
    played: 0,
    seeking: false,
  });
  useEffect(() => {
    getVideoDetails();
  }, []);

  const classes = useStyles();
  const { playing, muted, volume, playbackRate, played, seeking } = state;

  const playerRef = useRef(null);
  const playerContainerRef = useRef(null);

  // state changing codes.....
  const handlePlayPause = () => {
    setState({ ...state, playing: !state.playing });
  };
  const handleMute = () => {
    setState({ ...state, muted: !state.muted });
  };
  const handleRewind = () => {
    playerRef.current.seekTo(playerRef.current.getCurrentTime() - 10);
  };
  const handleFastForward = () => {
    playerRef.current.seekTo(playerRef.current.getCurrentTime() + 10);
  };
  const handleVolumeChange = (e, newValue) => {
    newValue === 0 ? setState({
      ...state,
      volume: parseFloat(newValue / 100),
      muted: true,
    })
      : setState({
        ...state,
        volume: parseFloat(newValue / 100),
        muted: false,
      });
  };
  const handleVolumeSeekDown = (e, newValue) => {
    newValue === 0 ? setState({
      ...state,
      volume: parseFloat(newValue / 100),
      muted: true,
    })
      : setState({
        ...state,
        volume: parseFloat(newValue / 100),
        muted: false,
      });
  };
  const handelPlaybackRate = (value) => {
    setState({
      ...state,
      playbackRate: value,
    });
  };
  const toggleFullScreen = () => {
    screenfull.toggle(playerContainerRef.current);
  };
  const handleProgress = (changeState) => {
    if (!seeking) {
      setState({ ...state, ...changeState });
    }
  };
  const handleSeekChange = (e, newValue) => {
    setState({ ...state, played: parseFloat(newValue / 100) });
  };
  const handleSeekMouseDown = (e) => {
    setState({ ...state, seeking: true });
  };
  const handleSeekMouseUp = (e, newValue) => {
    setState({ ...state, seeking: false });
    playerRef.current.seekTo(newValue / 100);
  };
  const currentTime = playerRef.current
    ? playerRef.current.getCurrentTime()
    : '00:00';
  const duration = playerRef.current
    ? playerRef.current.getDuration()
    : '00:00';
  const elapsedTime = format(currentTime);
  const totalDuration = format(duration);

  return (
    <>
      <div width="100%">
        <div ref={playerContainerRef} className={classes.playerWrapper}>
          <ReactPlayer
            ref={playerRef}
            width="100%"
            height="90vh"
            // url='https://www.youtube.com/watch?v=1StsoFtme-s'
            url={videoDetails?.video_file}
            muted={muted}
            playing={playing}
            volume={volume}
            playbackRate={playbackRate}
            onProgress={handleProgress}
          />
          <PlayerControls
            onPlayPause={handlePlayPause}
            playing={playing}
            onRewind={handleRewind}
            onFastForward={handleFastForward}
            muted={muted}
            onMute={handleMute}
            volume={volume}
            onVolumeChange={handleVolumeChange}
            onVolumeSeekUp={handleVolumeSeekDown}
            playbackRate={playbackRate}
            onPlaybackRateChange={handelPlaybackRate}
            onToggleFullScreen={toggleFullScreen}
            played={played}
            onSeek={handleSeekChange}
            onSeekMouseDown={handleSeekMouseDown}
            onSeekMouseUp={handleSeekMouseUp}
            currentTime={currentTime}
            elapsedTime={elapsedTime}
            totalDuration={totalDuration}
            title={videoDetails.title}
          />
        </div>
      </div>
    </>
  );
}
